.header-ctn {
    display: flex;
    flex-direction: row;
    background-color: #020203;
    grid-column: 1 / 3;
    grid-row: 1 / 2;
}

nav {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.logo {
    width: 178px;
    height: 61px;
    margin: 1em;
}

.header-link {
    color: #ffffff;
    text-decoration: none;
    font-size: 24px;
}

@media screen and (max-width: 1024px) {
    .logo {
        width: 140px;
        height: 40px;
    }
    .header-link {
        font-size: 20px;
    }
}
